.search-page {
  width: 100%;
  background-color: $dark5;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0 140px;

  &-settings {
    width: 28%;
    padding: 20px 60px 0 0;

    &-datepicker {

      &__header {
        display: block;
        width: 100%;
        margin-bottom: 5px;
      }
    }
  }

  &-content {
    width: 72%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 20px;

    &__header {
      display: block;
      width: 100%;
      margin-bottom: 20.5px;
    }
    &__item {
      margin-bottom: 20px;
    }
  }
}